<template>
  <div class="signup-container">
    <h2>회원가입</h2>
    <form @submit.prevent="signup">
      <div class="form-group">
        <label for="username">아이디:</label>
        <input
          type="text"
          style="width: 92%"
          id="userid"
          v-model="userid"
          name="userid"
          required
        />
      </div>
      <div class="form-group">
        <label for="password">비밀번호:</label>
        <input
          type="password"
          style="width: 92%"
          id="password"
          v-model="password"
          @input="showAdditionalFields = true"
          name="password"
          required
        />
      </div>
      <!-- 추가 정보 입력 폼 -->
      <div v-if="showAdditionalFields">
        <div class="form-group">
          <label for="name">이름:</label>
          <input
            type="text"
            style="width: 92%"
            id="name"
            v-model="name"
            name="name"
            required
          />
        </div>
        <div class="form-group">
          <label for="age">나이:</label>
          <input
            type="number"
            style="width: 92%"
            id="age"
            v-model="age"
            name="age"
            required
          />
        </div>
        <div class="form-group">
          <label for="gender">성별:</label>
          <select
            style="width: 92%"
            id="gender"
            v-model="gender"
            name="gender"
            required
          >
            <option value="male">남성</option>
            <option value="female">여성</option>
          </select>
        </div>
        <div class="form-group">
          <label for="email">이메일:</label>
          <input
            type="email"
            style="width: 92%"
            id="email"
            v-model="email"
            name="email"
            required
          />
        </div>
        <div class="form-group">
          <label for="student-id">학번:</label>
          <input
            type="text"
            style="width: 92%"
            id="student-id"
            v-model="studentId"
            name="studentId"
            required
          />
        </div>
        <div class="form-group">
          <label for="dob">생년월일:</label>
          <input
            type="date"
            style="width: 92%"
            id="dob"
            v-model="dob"
            name="dob"
            required
          />
        </div>
      </div>
      <!-- 추가 정보 입력 폼 끝 -->
      <div class="form-group">
        <button type="submit">회원가입</button>
      </div>
    </form>
    <p class="login-link">이미 계정이 있으신가요? <a style="text-decoration: underline;" @click="$router.push('/Login'); $store.commit('UserUnknownF')">로그인</a></p>
  </div>
</template>

<script>
export default {
    data() {
    return {
      userid: "",
      password: "",
      showAdditionalFields: false, // 추가 정보 입력 폼을 숨기기 위한 상태
      name: "",
      age: "",
      gender: "male",
      email: "",
      studentId: "",
      dob: ""
    };
    },
  methods: {
    signup() {
      // 회원가입 로직을 구현하고, 데이터를 서버로 전송
      // 성공적으로 가입되면 홈페이지로 리다이렉션 또는 다른 작업 수행
    }
  }
}
</script>

<style>
.signup-container {
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  padding: 20px;
  width: 90%;
  max-width: 350px;
}

.signup-container h2 {
  text-align: center;
  margin-bottom: 20px;
}

.form-group {
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  font-weight: bold;
}

.form-group input[type="text"],
.form-group input[type="password"],
.form-group input[type="number"],
.form-group input[type="email"],
.form-group input[type="date"],
.form-group select {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

.form-group button {
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
}

.form-group button:hover {
  background-color: #0056b3;
}

.login-link {
  text-align: center;
  margin-top: 10px;
}
</style>
