import { createRouter, createWebHistory } from 'vue-router'
import LoginTabVue from './components/LoginTab.vue';
import RegisterTabVue from './components/RegisterTab.vue';

const routes = [
  {
    path: '/Login',
    component: LoginTabVue,
  },
  {
    path: '/Register',
    component: RegisterTabVue,
  },
];
const router = createRouter({
  history: createWebHistory(),
  routes,
})

export default router; 