import { createStore } from 'vuex'

const store = createStore({
  state(){
    return {
        UserStatus : "Unknown",
    }
  },
  mutations :{
    UserRegisterF(state){
        state.UserStatus = 'Register'
    },
    UserUnknownF(state){
        state.UserStatus = 'Unknown'
    }
  }
})

export default store