<template>
  <router-view @onLogin="loginEvent; UserInformations = $event"></router-view>
</template>

<script>

export default {
  name: "App",
  components: {
  },
  data() {
    return {
      UserInformations : {}
    }
  },
  methods: {
    loginEvent(){
      this.UserStatus = "User"
      console.log(this.UserInformations)
    }
  },
  mounted(){
    
  },
  
  watch:{
    $route(to){
      if(to.path == '/Register')
      {
        this.$store.commit('UserRegisterF')
      }
      else if(this.$store.state.UserStatus == "Unknown")
      {
        this.$router.push('/Login')
      }
      else if(this.$store.state.UserStatus == "Register" && to.path != '/Register')
      {
        this.$router.push('/Login')
        this.$store.commit('UserUnknownF')
      }
    }
  },
};
</script>

